import Engine from './utils/engine';
// import Engine from './utils/engine3d';

import { initialiseWebcam, getMediaInfo } from './utils/camera';
import { loadImage, drawCenteredImage, drawCenteredText, getBrightness, colorToString } from './utils/utils';
import { getPointOnArc } from './utils/paths';

const WIDTH = 480;
const HEIGHT = 480;

const BG = 'blue';
const FG = 'white';

const PADDING = 20;

const el = document.getElementById('canvas');
// const el = document.getElementById('container');

const app = new Engine(el, {
  debug: false,
  width: WIDTH,
  height: HEIGHT,
  pixelRatio: 1
});

function drawPixel(x, y, size, color = [255, 100, 50]) {
  ctx.save();
  ctx.translate(x, y);
  let wid = size / 3;
  for (let i = 0; i < 3; i++) {
    let col = [0,0,0];
    col[i] = 255;
    ctx.fillStyle = colorToString({
      r: col[0],
      g: col[1],
      b: col[2],
    });
    // ctx.fillStyle = preColours[i];
    // ctx.fillRect(i * wid, 0, wid, size * (color[i] / 255));

    const { x: _x, y: _y } = getPointOnArc(0, 0, ((Math.PI * 2) / 3) * i, (color[i] / 255) * 100);
    ctx.fillRect(_x, _y, size * 0.5, size * 0.5);
  }
  ctx.restore();
}

function intrinsicHeight(destWid, destHei, wid, hei) {
  return (destWid / wid) * hei;
}

function intrinsicWidth(destWid, destHei, wid, hei) {
  return (destHei / hei) * wid;
}

let scale = 12;
let renderScale = 0.5;

// loadImage(imgSrc)
initialiseWebcam()
  .then(el => {
    const { width: videoWidth, height: videoHeight } = getMediaInfo();

    app.onRender(({ frame, width, height, debug }) => {
      let scaledWid = width / scale;
      let scaledHei = height / scale;

      if (videoWidth >= videoHeight) {
        ctx.drawImage(el, 0, 0, intrinsicWidth(scaledWid, scaledHei, videoWidth, videoHeight), scaledHei);
      } else {

        ctx.drawImage(el, 0, 0, scaledWid, intrinsicHeight(scaledWid, scaledHei, videoWidth, videoHeight));
      }

      let imageData = ctx.getImageData(0, 0, scaledWid, scaledHei);
      ctx.fillStyle = '#111';
      ctx.fillRect(0, 0, width, height);

      ctx.save();
      ctx.translate(width * ((1 - renderScale) / 2), height * ((1 - renderScale) / 2));
      ctx.globalCompositeOperation = 'screen';
      ctx.scale(1 * renderScale, 1 * renderScale);
      for (let i = 0; i < imageData.data.length; i += 4) {
        let x = (i / 4) % (scaledWid);
        let y = Math.floor((i / 4) / scaledHei);
        drawPixel(x * scale, y * scale, scale, [imageData.data[i], imageData.data[i + 1], imageData.data[i + 2]]);
      }
      ctx.restore();
    });

    app.start();
  })
